.contacts {
  &__title {
    margin: 0;
    margin-bottom: 48px;
    font-weight: 600;
    font-size: 32px;
    line-height: 48px;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 8px;
    text-transform: uppercase;
    letter-spacing: 3px;
    opacity: 0.5;

    &--socials {
      margin-bottom: 16px;
    }
  }

  &__link {
    display: inline-block;
    margin-bottom: 40px;

    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    color: inherit;
    text-decoration: none;

    @include on-tablet {
      margin-bottom: 48px;
    }

    @include pointer-cursor;
    @include on-hover(color, $blue-color);
  }

  &__social-media {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;
    width: 160px;
  }

  &__social-link {
    display: block;
    width: 32px;
    height: 32px;
    text-decoration: none;

    background-image: url(../images/icons/facebook-icon.svg);
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center;

    @include pointer-cursor;
    @include on-hover(
      background-image,
      url(../images/icons/facebook-icon-hover.svg)
    );
    @include on-hover(transform, scale(1.3));

    &--twitter {
      background-image: url(../images/icons/twitter-icon.svg);

      @include on-hover(
        background-image,
        url(../images/icons/twitter-icon-hover.svg)
      );
    }

    &--instagram {
      background-image: url(../images/icons/instagram-icon.svg);

      @include on-hover(
        background-image,
        url(../images/icons/instagram-icon-hover.svg)
      );
    }
  }
}
